import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-form-wizard',
  templateUrl: './form-wizard.component.html',
  styleUrls: ['./form-wizard.component.scss']
})
export class FormWizardComponent implements OnInit {

  // tslint:disable-next-line: no-input-rename
  @Input('steps') steps: number;
  // tslint:disable-next-line: no-input-rename
  @Input('currentStep') currentStep: number;
  // tslint:disable-next-line: no-output-rename
  @Output('selectStep') selectStep = new EventEmitter();
  stepList: any[] = [];

  constructor() {
    this.currentStep = this.currentStep ? this.currentStep : 1;
    this.steps = this.steps ? this.steps : 1;
  }
  ngOnInit() {
    for (let i = 1; i <= this.steps; i += 1) {
      const selected = i <= this.currentStep ? true : false;
      this.stepList.push({
        numStep: i,
        statusStep: selected
      });
    }
  }

  onSelectStep(step: any) {
    this.selectStep.emit(step.numStep);
  }
}
