import { Injectable } from '@angular/core';
import { CanLoad, Route, UrlSegment, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { SendInformationService } from '../../service/SendInformation/send-information.service';
import { STEP_ID } from '../../CONST';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ForbiddenGuard implements CanLoad {

  guards: boolean;
  constructor(
    private guard: SendInformationService<any>,
    private router: Router
  ) {
    this.guards = environment.guards;
  }

  canLoad(
    route: Route,
    segments: UrlSegment[]
  ): Observable<boolean> | boolean {
      if ( !this.guards ) {
        return true;
      }
      if ( !this.guard.lastValue(STEP_ID) ) {
        this.router.navigate([route.data.stepForbidden]);
      }
      return true;
  }
}
